<template>
    <main>
        <h1></h1>
        <div id='form'>
            <div class='formItem' v-if='!msg'>
                <label for="username"></label>
                <input type="text" name="username" id='username' autofocus required
                    placeholder='' v-model="username">
            </div>
            <div class='formItem' v-if='!msg'>
                <label for="password"></label>
                <input type="password" name="password" id='password' required placeholder='' v-model="password">
            </div>
            <div class='formItem' v-if='!msg'>
                <label for="checkpassword"></label>
                <input type="password" name="checkpassword" id='checkpassword' required placeholder=''>
            </div>
            <div class='formItem' v-if='!msg'>
                <label for="name"></label>
                <input type="text" name="name" id='name' required placeholder='' v-model="name">
            </div>
            <div class='formItem' v-if='!msg'>
                <label for="idNumber"></label>
                <input type="text" name="idNumber" id='idNumber' required placeholder='' v-model="idNumber">
            </div>
            <div class='formItem' v-if='!msg'>
                <label for="phone"></label>
                <input type="text" name="phone" id='phone' required placeholder=''  v-model="phone">
            </div>
            <div class='formItem' v-if='!msg'>
                <label for="email"></label>
                <input type="email" name="email" id='email' required placeholder='' v-model="email">
            </div>
            <div class='formItem' v-if='!msg'>
                <label for="address"></label>
                <Address @Address='AddressSet'></Address>
                <textarea rows="1" name="address" id='address' required v-model="address2">  </textarea>
            </div>
            <div class='formItem' v-if='!msg'>
                <input type="submit" value='' @click='submitForm($event)'>
            </div>
            <div class="msg" v-if='msg' v-text='msg'>

            </div>
        </div>

    </main>
</template>

<script>
import Address from '@/components/Address.vue'
export default ({
    data(){
        return{
            username:null,
            password:null,
            name:null,
            idNumber:null,
            phone:null,
            email:null,
            address1:null,
            address2:null,
            msg:null,
            captcha:null,
            dataKey:1,
        }
    },
    components:{
        Address
    },
    methods:{
        submitForm(e){
            // console.log(this.$data);
            const emailRules = /^\w+([-+.']\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
            const passwordRules = /^(?=.*[a-zA-Z])(?=.*\d)[a-zA-Z\d]{5,}$/;
            const phoneRules = /^09\d{8}$/;
            const idRules = /^[A-Z]{1}[1-2]{1}[0-9]{8}$/;
            if (!passwordRules.test(this.username)) {
                document.querySelector('#username').focus();
                alert('');
                e.preventDefault();
                return;
            }
            if (!passwordRules.test(this.password)) {
                document.querySelector('#password').focus();
                alert('');
                e.preventDefault();
                return;
            }
            else if (this.password !== document.querySelector('#checkpassword').value) {
                document.querySelector('#checkpassword').focus();
                alert('');
                e.preventDefault();
                return;
            }
            if (!idRules.test(this.idNumber)) {
                document.querySelector('#idNumber').focus();
                alert('');
                e.preventDefault();
                return;
            }
            if (!phoneRules.test(this.phone)) {
                document.querySelector('#phone').focus();
                alert('');
                e.preventDefault();
                return;
            }
            if (!emailRules.test(this.email)) {
                document.querySelector('#email').focus();
                alert('');
                e.preventDefault();
                return;
            }
            axios.post('/api/data.php',JSON.stringify(this.$data))
                    .then(res=>{
                        this.msg = res.data.msg;
                        if(res.data.status == true){
                            setTimeout( ()=>{
                                this.msg = null;
                                this.$router.push('/login');
                            },2000);
                        }
                        else{
                            setTimeout( ()=>{
                                this.msg = null;
                            },2000);
                        }
                    })
        },
        AddressSet(val){
            this.address1 = val;
        }
    },
})
</script>

<style scoped>
    main {
        display: grid;
        grid-template-rows: 140px 60px 1fr;
        grid-template-columns: 20vw 60vw 20vw;
        background-color: #000;
        padding-bottom: 40px;
    }

    main h1 {
        grid-row: 2/3;
        grid-column: 2/3;
        font-size: 1.9rem;
        font-weight: bold;
        color: white;
        padding-left: 5px;
        position: relative;
        text-align: left;
    }

    main h1::after {
        content: '';
        position: absolute;
        border-bottom: 3px red solid;
        width: 100%;
        left: 0px;
        bottom: 20px;
    }

    main #form {
        border: 3px solid rgb(235, 235, 235);
        border-radius: 10px;
        grid-row: 3/4;
        grid-column: 2/3;
        padding: 2vh 5vw 4vh 5vw;
    }

    main #form .formItem {
        display: flex;
        flex-wrap: wrap;
    }

    main #form .formItem+.formItem {
        margin-top: 5vh;
    }

    main #form .formItem label {
        font-size: 18px;
        line-height: 30px;
        height: 30px;
        color: white;
        font-weight: bold;
        margin-right: 2vw;
        width: 90px;
        text-align: center;
    }

    main #form .formItem input {
        border: 2px solid rgb(235, 235, 235);
        border-radius: 30px;
        background-color: black;
        height: 20px;
        color: white;
        width: 100%;
        max-width: 40vw;
        font-size: 15px;
    }

    main #form .formItem textarea {
        background-color: black;
        border: 2px solid rgb(235, 235, 235);
        border-radius: 5px;
        color: white;
        width: 100%;
        max-width: 47vw;
        font-size: 18px;
        resize: none;
        margin-top: 5px;
        height: 30px;
    }

    main #form .formItem input:focus {
        outline: none;
    }

    main #form .formItem input[type='submit'] {
        margin-left: auto;
        margin-right: 2.4vw;
        height: 35px;
        width: 100px;
        font-weight: bold;
        cursor: pointer;
    }

    main #form .formItem input[type='submit']:hover {
        background-color: rgb(189, 189, 189);
        color: black
    }

    main #form .msg{
        font-size: 1.6rem;
        color: white ;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
    }

    main #form .formItem input[type='submit']:active {
        background-color: rgb(90, 90, 90);
        color: rgb(226, 226, 226)
    }

    @media screen and (max-width:1300px) {
        main #form .formItem label {
            text-align: inherit;
            width:100%;
        }

        main #form .formItem input {
            max-width: inherit;
        }

        main #form .formItem textarea {
            max-width: inherit;
        }

        main #form .formItem input[type='submit'] {
            margin-right: inherit;
        }
    }
</style>
